.head__menu {
  justify-content: space-between;
  align-items: center;
  margin-bottom: 15px;
  padding-top: 40px;
  display: flex;
}

.logo {
  align-items: center;
  gap: 12px;
  font-family: Roboto;
  font-size: 30px;
  font-style: normal;
  font-weight: 500;
  line-height: 1.16;
  display: flex;
}

.logo__film {
  fill: #fff;
  stroke: #fff;
  animation: logo 3s infinite;
}

@keyframes logo {
  0% {
    stroke: #ff6b08;
    transform: scale(1.5);
  }

  50% {
    stroke: #fff;
    transform: scale(1);
  }

  100% {
    stroke: #ff6b08;
    transform: scale(1.5);
  }
}

.nav__menu {
  gap: 40px;
  display: flex;
  position: relative;
}

.search {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
}

.nav__menu__link {
  cursor: pointer;
  color: #fff;
  font-family: Roboto;
  font-size: 14px;
  font-style: normal;
  font-weight: 500;
  line-height: 1.15;
  position: relative;
}

.nav__menu__link:hover:after, .nav__menu__link:focus:after {
  content: "";
  opacity: 1;
  height: 3px;
  background-color: #ff001b;
  transition: color, opacity .3s cubic-bezier(.4, 0, .2, 1);
  display: block;
  left: 0;
}

.nav__menu__link--active:after {
  content: "";
  width: 100%;
  opacity: 0;
  height: 3px;
  content: "";
  opacity: 1;
  height: 3px;
  background-color: #ff001b;
  transition: color, opacity .3s cubic-bezier(.4, 0, .2, 1);
  display: block;
  position: absolute;
  left: 0;
}

