.loader {
  z-index: 1;
  width: 100%;
  height: 100%;
  pointer-events: none;
  justify-content: center;
  align-items: center;
  display: flex;
  position: absolute;
}

.loader .loader_inner {
  width: 100px;
  height: 100px;
  bottom: 50%;
  right: 50%;
  transform: translate(-50%, -50%);
}

.loader span {
  width: 40%;
  height: 40%;
  border-radius: 50%;
  animation: loading 2s infinite;
  position: absolute;
}

.loader span:nth-child(1) {
  background: #19a6bc;
  animation-delay: .5s;
}

.loader span:nth-child(2) {
  background: #f63d3a;
  animation-delay: 1s;
}

.loader span:nth-child(3) {
  background: #f0a543;
  animation-delay: 1.5s;
}

.loader span:nth-child(4) {
  background: #193848;
  animation-delay: 2s;
}

@keyframes loading {
  0%, 100% {
    transform: translate(0);
  }

  25% {
    transform: translate(80px);
  }

  50% {
    transform: translate(80px, 80px);
  }

  75% {
    transform: translate(0, 80px);
  }
}

.loader.fadeOut {
  animation: fadeOut 1s alternate;
}

@keyframes fadeOut {
  to {
    opacity: 0;
  }
}

